																<div class="item__row">
																	<a class="link__module" href="">
																		<svg width="9" height="17" fill="none" xmlns="http://www.w3.org/2000/svg">
																			<path d="M.4 14.4l5.3-5.9L.4 2.58A1.64 1.64 0 0 1 .4.44c.53-.59 1.4-.59 1.93 0L8.6 7.43c.53.6.53 1.55 0 2.14l-6.27 6.98c-.54.6-1.4.6-1.93 0a1.68 1.68 0 0 1 0-2.16z" fill="#4F4F4F"/>
