                    </div>
                </section>
            </main>
        </template>
    </div>
</template>

<script>
  import { mapGetters, mapMutations } from 'vuex';
  import {
    getCourse,
    joinCourse,
    getCourseLearner
  } from '../../utils/api/course';
  import { getUserProfile } from '../../utils/api/user';
  import Loader from '../../components/Loader';
  import CourseQuiz from './CourseQuiz';
  import CourseDiscussion from './CourseDiscussion';
  import setActiveTab from '../../utils/tabPanel';
  import { success, fail } from '../../utils/toast';

  export default {
    name: 'CourseDetail',
    data() {
      return {
        loadingCourse: true,
        itemData: {},
        courseLoaded: false,
        lessons: [],
        userCourses: [],
        userCoursesLoaded: false,
        userId: this.$session.get('id')
      };
    },
    created() {
      this.getCourse();
      this.courseLearner();
      this.getUserProfile();
    },
    methods: {
      ...mapMutations([
        'setJoinedCourse',
        'resetJoinedCourse'
      ]),
      courseLearner() {
        getCourseLearner(this.$route.params.id, this.$session.get('id'))
          .then((response) => {
            if (response.status === 200) {
              this.setJoinedCourse(!!response.data.data.class_id);
              return true;
            }
            this.resetJoinedCourse();
            return false;
          })
          .catch((error) => {
            this.resetJoinedCourse();
            return error;
          });
      },
      getCourse() {
        getCourse(this.$route.params.id, this.userId)
          .then((response) => {
            if (response.status === 200) {
              this.itemData = response.data.data;
              this.lessons = response.data.data.lessons;
              this.courseLoaded = true;
              return true;
            }
            this.courseLoaded = false;
            return false;
          }).catch((error) => {
          this.courseLoaded = false;
          return error;
        }).finally(() => {
          this.loadingCourse = false;
        });
      },
      activateTab(ref) {
        return setActiveTab(ref, this.$refs);
      },
      joinCourse() {
        joinCourse(this.$route.params.id, { user_id: this.userId })
          .then((response) => {
            if (response.status === 201) {
              this.setJoinedCourse(true);
              success({
                title: 'Success! You have successfully joined the course!.'
              });
            }
          }).catch((error) => {
          fail({
            title: 'Oops! Something went wrong',
            text: error.data
          });
          return error;
        });
      },
      getUserProfile() {
        getUserProfile(this.userId)
          .then((response) => {
            if (response.status === 200) {
              this.userCourses = response.data.data.classes;
              this.userCoursesLoaded = true;
              const joinedId = this.userCourses.find(course => course.class_id === Number(this.$route.params.id)).class_id;
              if (joinedId) {
                this.setJoinedCourse(true);
              } else {
                this.resetJoinedCourse();
              }
            }
            this.userCoursesLoaded = false;
            return false;
          }).catch((error) => {
          this.userCoursesLoaded = false;
          return error;
        });
      }
    },
    computed: {
      ...mapGetters({
        joinedCourse: 'getJoinedStatus'
      })
    },
    components: {
      Loader,
      CourseDiscussion,
      CourseQuiz
    },
    filters: {
      date(value) {
        return moment(value).fromNow();
      }
    }
  };
</script>

