<template>
	<div>
		<template v-if="attempt >= itemData.exam_attempts">
			<div class="alert__wrapper">
				<p class="alert alert-danger" role="alert">
					You are out of attempts and can not take this exam again
				</p>
			</div>
		</template>
		<template v-else-if="quizResult">
			<div class="result__wrapper">
				<p>This quiz has been taken and submitted already</p>
				<h3 class="bold">Your Score is {{quizResult.score}}%</h3>
			</div>
		</template>
		<template v-else>
			<template v-if="quizLoaded && quizzes.length">
				<div class="quiz">
					<div class="module__wrapper module__wrapper--between">
						<div>
							Attempts: <span class="bold">{{attempt}}/{{itemData.exam_attempts}}</span>
						</div>
						<div class="time__wrapper">
							<h5 class="title">Countdown Timer</h5>
							<span class="time">{{minutes}}:{{seconds}}</span>
						</div>
					</div>
					<div class="quiz__meta">
						<span ref="progress" class="level">course progress: 
							<span v-if="progress" class="bold">{{`${progress}%`}}</span>
							<span v-else class="bold">0%</span>
						</span>
					</div>
					<div class="quiz__main">
						<template v-if="quizIntro">
							<div class="center">
								<h4>Welcome to the quiz</h4>
								<p class="pb-10">Click the button below to start the timer for your quiz</p>
								<button @click="startQuiz" type="button" class="btn__primary btn__primary--alt">Start</button>
							</div>
						</template>
						<template v-if="quizStart">
							<div class="quiz__heading">
								<h4 class="quiz__primary">{{ itemData.title }}</h4>
								<h5 class="quiz__secondary">Please answer all question.</h5>
							</div>
							<div class="quiz__body">
								<form @submit.prevent="submitQuiz" class="form__quiz" action="">
									<Question 
										:question="quizzes[currentQuizIndex]"
										:question-index="currentQuizIndex + 1"
										:reset="reset"/>
									<template v-if="currentQuizIndex === (quizzes.length - 1)">
										<button 
											type="submit" 
											class="btn btn__quiz"
											@click.prevent="submitQuiz">Submit
										</button>
									</template>
									<template v-else>
										<button
											@click.prevent="nextQuiz"
											type="submit" 
											class="btn btn__quiz">Next</button>
									</template>
								</form>
							</div>
						</template>
						<template v-if="quizSubmitted">
							<div class="center">
								<h4>Well done!</h4>
								<p>Your score is {{percentage}}</p>
								<h5 class="bold">You earned a badge</h5>
							</div>
						</template>
					</div>
				</div>
			</template>
			<template v-else-if="quizLoaded">
				<div class="result__wrapper">
					<p>No quiz available yet</p>
				</div>
			</template>
			<template v-else>
				<div class="result__wrapper">
					<p>Error Loading quiz</p>
				</div>
			</template>
		</template>
	</div>
	
</template>

<script>
import { mapGetters } from 'vuex';
import { getCourseQuiz } from '../../utils/api/course';
import { submitScores } from '../../utils/api/quiz';
import { getUserScores } from '../../utils/api/user';
import eventHub from '../../utils/eventhub';
import Question from './Question';
import { success, fail } from '../../utils/toast';
import Loader from '../../components/Loader';
import { getUserCourseAttempts } from '../../utils/api/course';

export default {
	name: 'CourseQuiz',
	props: {
		done: {
			type: Boolean,
			default() {
				return false;
			}
		},
		itemData: {
			type: Object,
			default() {
				return {

				};
			}
		}
	},
	data() {
		return {
			quizzes: [],
			loadingQuiz: false,
			quizLoaded: false,
			total: 0,
			currentQuizIndex: 0,
			quizIntro: false,
			quizStart: false,
			quizSubmitted: false,
			quizResult: null,
			result: null,
			score: 0,
			timer: null,
			timelimit: 1,
			totalTime: 0,
			percentage: 0,
			response: [],
			reset: false,
			attempt: 0
		};
	},
	created() {
		this.getQuiz();
		this.getScores();
		this.getUserAttempts();
		eventHub.$on('result', (payload) => {
			this.score += payload;
		});
	},
	mounted() {
		if (this.done) {
			this.getScores();
		}
	},
	watch: {
		score(value) {
			this.percentage = (value / this.quizzes.length) * 100;
		}
	},
	computed: {
		...mapGetters({
			joinedCourse: 'getJoinedStatus',
			user: 'getLoggedUser'
		}),
		progress() {
			if (this.quizStart) {
				return ((this.currentQuizIndex + 1) / this.quizzes.length) * 100;
			}
			return 0;
		},
		minutes() {
			const minutes = Math.floor(this.totalTime / 60); 
			return minutes < 10 ? `0${minutes}` : minutes;
		},
		seconds() {
			const seconds = Math.floor(this.totalTime - (this.minutes * 60));
			return seconds < 10 ? `0${seconds}` : seconds;
		}
	},
	methods: {
		getScores() {
			getUserScores(this.$session.get('id'))
				.then((response) => {
					if (response.status === 200) {
						const courseId = this.$route.params.id || this.$route.params.onlineClassId;
						this.quizResult = response.data.data.find(item => item.online_class_id === courseId);
					}
				})
				.catch((error) => {
					return error;
				});
		},
		getQuiz() {
			getCourseQuiz(this.$route.params.id || this.$route.params.onlineClassId)
				.then((response) => {
					if (response.status === 200) {
						this.quizzes = response.data.data;
						this.total = response.data.total;
						this.loadingQuiz = false;
						this.quizLoaded = true;
						this.quizIntro = true;
						const time = this.quizzes.reduce((a, b) => {
							return a + b.duration;
						}, 0);
						this.totalTime = this.timelimit * time;
					}
				})
				.catch((error) => {
					this.quizLoaded = false;
					this.loadingQuiz = false;
					return error;
				});
			return true;
		},
		getUserAttempts() {
			getUserCourseAttempts(this.$route.params.id, this.user.id)
				.then((response) => {
					if (response.status === 200) {
						this.attempt = response.data.data.attempt;
						console.log(this.attempt);
					}
				})
				.catch((error) => {
					return error;
				});
		},
		startQuiz() {
			this.quizIntro = false;
			this.quizStart = true;
			this.setProgressWidth();
			this.startTimer();
		},
		submitQuiz() {
			this.resetTimer();
			submitScores({
				online_class_id: this.$route.params.id || this.$route.params.onlineClassId,
				user_id: this.$session.get('id'),
				score: this.percentage
			})
				.then((response) => {
					if (response.status === 201) {
						this.quizStart = false;
						this.quizSubmitted = true;
						this.getScores();
						success({
							title: 'Success! You have completed the quiz!.'
						});
					}
				})
				.catch((error) => {
					fail({
						title: 'Oops! Something went wrong',
						text: error.data
					});
					return error;
				});	
		},
		nextQuiz() {
			this.currentQuizIndex += 1;
			this.reset = true;
			setTimeout(() => {
				this.reset = false;
			}, 500);
		},
		startTimer() {
			this.timer = setInterval(() => this.countdown(), 1000);
		},
		stopTimer() {
			clearInterval(this.timer);
			this.timer = null;
		},
		resetTimer() {
			this.totalTime = 1 * 60;
			clearInterval(this.timer);
			this.timer = null;
		},
		countdown() {
			if (this.totalTime >= 1) {
				this.totalTime -= 1;
			} else {
				this.totalTime = 0;
				this.submitQuiz();
				this.resetTimer();
			}
		},
		setProgressWidth() {
			const width = this.progress * 100;
			this.$refs.progress.style.width = `${Number(width.toFixed(2))}%`;
		}
	},
	components: {
		Question,
		Loader
	}
};
</script>

<style lang="scss" scoped>
	.center {
		text-align: center;
	}
	.level {
		white-space: nowrap;
	}
	.result__wrapper {
		text-align: center;
		padding: 30px;
		.bold {
			padding-top: 20px;
		}
	}
	.alert__wrapper {
		margin: 30px;
	}
</style>
