import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
 * Fetches the a user using the id
 * @param {Number} id - id of user
 * @returns {Promise} user - a promise of user to be resolved.
 */
export function getUserProfile(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/users/${id}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}


/**
 * Fetches the quizzes a user has taken
 * @param {Number} id - id of user
 * @returns {Promise} quizzes - a promise of quizzes to be resolved.
 */
export function getUserQuizResponse(id) {
	if (isLoggedIn) {
		return axios.get(`/v1/quizresponses/${id}/user`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}
