/**
 * Updates a user
 * @param {Number} id - Id of the user to be updated
 * @param {Object} userData - the user data object
 * @returns {Promise} user - a promise of the updated user.
 */
export function updateUser(id, userData) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/users/${id}`, userData);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}

/**
 * Updates a user's avatar
 * @param {Number} id - Id of the user to be updated
 * @param {Object} avatar - the base64 image object to be updated
 * @returns {Promise} a promise of the updated user.
 */
export function updateAvatar(id, avatar) {
	if (isLoggedIn()) {
		return axios.patch(`v1/users/${id}/avatar`, { file: avatar });
	}
	return Promise.resolve(new Response(
		{
			message: 'Unauthorised'
		},
		{
			status: 403,
			statusText: 'Unauthorised'
		}
	));
}

/**
 * Uploads user credentials
 * @param {Number} id - Id of the user to be updated
 * @param {Object} credentials - the user's credentials object
 * @returns {Promise} user - a promise of the updated user.
 */
export function uploadCredential(id, credential) {
	if (isLoggedIn()) {
		return axios.post('v1/credentials', credential);
	}
	return Promise.resolve(new Response(
		{
			message: 'Unauthorised'
		},
		{
			status: 403,
			statusText: 'Unauthorised'
		}
	));
}

