/* Updates a user's bank
 * @param {Number} id - ID of the credential to be deleted
 * @param {Number} userId - ID of the user
 * @returns {Promise} user - a promise of the updated user.
 */
export function deleteCredential(id, userId) {
	if (isLoggedIn()) {
		return axios.delete(`/v1/credentials/${id}/user/${userId}`);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403, statusText: 'Unauthorised'
	}));
}

